@import url(https://fonts.googleapis.com/css2?family=Roboto&display=swap);
@font-face{font-family:CoreSansG-Regular;src:url("/fonts/CoreSansG-Regular.ttf")}@font-face{font-family:CoreSansG-Light;src:url("/fonts/CoreSansG-ExtraLight.ttf")}@font-face{font-family:CoreSansG-Bold;src:url("/fonts/CoreSansG-Bold.ttf")}html,body,ul,li,h1{margin:0;padding:0;list-style-type:none}a{text-decoration:none;color:#fff}.App{text-align:center}.heading{margin-top:80px;font-family:'CoreSansG-Light'}

.header{width:100%;height:100px;background:linear-gradient(90deg, #5A4BE6 .26%, #73AFF7 47.1%, #93D0D9 99.8%);text-align:center;line-height:100px;text-transform:uppercase;font-family:CoreSansG-Regular;font-weight:bold;font-size:32px;letter-spacing:2px;color:#CFE0F4;position:relative}.language-toggler{height:19px;position:absolute;top:0;right:5%}.language-toggler__link{font-family:Roboto, sans-serif;font-size:16px;letter-spacing:-0.2px;margin-right:10px;background-color:transparent;border:none;outline:none;color:#fff}.language-toggler__link--active{color:#587BC3}.language-toggler__link:first-child::after{content:" | ";color:#fff}

.section{padding:0px calc(50% - 480px)}.card__heading{font-family:CoreSansG-Bold, sans-serif;width:404px;display:block;text-align:left;font-size:18px}.card__form{margin-top:28px;border:1px solid #E4E4E4;border-radius:4px;display:flex;flex-direction:column;padding:40px 100px;align-items:center}.card__input{font-size:16px;font-family:CoreSansG-Regular, sans-serif;box-sizing:border-box;margin-top:16px;display:block;border:1px solid #CCCCCC;border-radius:4px;width:404px;height:56px;outline:none;transition:border 0.3s}.card__input:focus{border:1px solid #93D0D9}.card__input--small{width:194px}.card__input--error{border-color:#FF8D8D}.card__wrapper{width:404px;display:flex;justify-content:space-between}.card__info{display:block;margin-top:19px;width:394px;text-align:left;font-family:CoreSansG-Regular, sans-serif;font-size:16px;color:#808080;line-height:20px}.card__button{margin-top:32px;box-sizing:border-box;width:404px;height:56px;background:linear-gradient(122.5deg, #5A4BE6 -33.07%, #73AFF7 48.35%, #93D0D9 139.94%);border-radius:4px;outline:none;font-family:CoreSansG-Regular, sans-serif;font-size:16px;line-height:17px;color:#FFF;border-radius:4px;border:none}

